@import url(https://fonts.googleapis.com/css2?family=Italianno&display=swap);
.skillContainer{
    width: 100px;
    height: 100px;
    overflow: hidden;

}

html::-webkit-scrollbar {
    width:0;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#scrollpath{
    position: fixed;
    top:0;
    right:0;
    width: 6px;
    height:100%;
    background: rgba(255,255,255,0.05);
}
#progressbar{
    position: fixed;
    top:0;
    right:0;
    width: 6px;
    height:100%;
    background: linear-gradient(to top,rgb(102, 23, 206), #008aff,#00ffe7);
    animation: scrollanimate 3s linear infinite;
}
@keyframes scrollanimate {
    0%,100%{
        -webkit-filter: hue-rotate(0deg);
                filter: hue-rotate(0deg);
    }
    50%{
        -webkit-filter: hue-rotate(20deg);
                filter: hue-rotate(20deg);
    }
}

.animateme2{
    animation: animate2 10s linear infinite;
}
.animateme1{
    animation: animate1 15s linear infinite;
}
.animateme3{
    animation: animate3 17s linear infinite;
}
.animateme4{
    animation: animate4 10s linear infinite;
}
.animatehand{
    animation: animate4 2s !important;
}
@keyframes animate1 {
    0%{
        transform: scale(0.5)
    }
    25%{
        transform: scale(0.6)
    }
    50%{
        transform: scale(1)
    }
    75%{
        transform:scale(0.6)
    }
    100%{
        transform: scale(0.5)
    }
}
@keyframes animate2 {
    0%{
        transform: scale(0.5)
    }
    25%{
        transform: scale(0.6)
    }
    50%{
        transform: scale(1)
    }
    75%{
        transform:scale(0.6)
    }
    100%{
        transform: scale(0.5)
    }
}
@keyframes animate3 {
    0%{
        transform: scale(0.5)
    }
    25%{
        transform: scale(0.6)
    }
    50%{
        transform: scale(1)
    }
    75%{
        transform:scale(0.6)
    }
    100%{
        transform: scale(0.5)
    }
}
@keyframes animate4 {
    0%{
        transform: scale(0.7)
    }
    25%{
        transform: scale(0.6)
    }
    50%{
        transform: scale(1.1)
    }
    75%{
        transform:scale(0.6)
    }
    100%{
        transform: scale(0.7)
    }
}

@keyframes animate5 {
    0%{
        transform: rotate(0deg);
    }
    50%{
        transform: rotate(90deg);
    }
    100%{
        transform: rotate(0deg);
    }

}
.adjustme{
    width: 100% !important;
    height: 100vh !important;
    overflow: hidden !important;
